






































































import LocationIdentifier from '../models/LocationIdentifier';
import Supercluster from '../models/Supercluster';
import Vue from 'vue';

export default Vue.extend({
  props: {
  },
  data: function (){
    return {
      supercluster: new Supercluster(
        this.$route.query.supercluster as string,
        new LocationIdentifier(null, 'universe', this.$route.query.universe as string)
      )
    }
  },
  methods: {
  },
  created() {
  }
})
