












































































import Vue from 'vue'
import Measurement from '@/components/Measurement.vue'
import Visualisation from '@/components/Visualisation.vue'
import Universe from '../models/Universe'
import Supercluster from '../models/Supercluster'
import UniverseSceneGenerator from '../scenes/UniverseSceneGenerator'
import { significantUnits } from "../models/BigNumber"

export default Vue.extend({
  components: {
    Measurement,
    Visualisation,
    // GChart
  },
  props: {
  },
  data: function (){
    return {
      universe: new Universe(this.$route.query.universe as string),
      breadcrumbs: [
          {
            text: 'Multiverse',
            href: '/',
          },
          {
            text: `Universe: ${this.$route.query.universe}`,
            disabled: true
          },
        ],
      zoom: 50,
      sceneGenerator: new UniverseSceneGenerator(),
      superclusterTableHeaders: [
          {
            text: 'Name',
            align: 'start',
            sortable: true,
            value: 'name',
          },
          {
            text: 'Radius',
            align: 'start',
            sortable: true,
            value: 'radius',
          },
          {
            text: 'Density',
            align: 'start',
            sortable: true,
            value: 'density',
          },
          {
            text: 'Distance from Centre',
            align: 'start',
            sortable: true,
            value: 'distance',
          },
          {
            text: 'Galaxy Groups',
            align: 'start',
            sortable: true,
            value: 'galaxyGroupCount',
          },
          {
            text: 'Galaxies',
            align: 'start',
            sortable: true,
            value: 'estimatedGalaxyCount',
          },
          {
            text: 'Stars',
            align: 'start',
            sortable: true,
            value: 'stars',
          },
          {
            text: 'Prominent Colour',
            align: 'start',
            sortable: true,
            value: 'colour',
          },
      ],
      options: { sortBy: ['name'], sortDesc: [true], itemsPerPage: 5 }
    }
  },
  mounted: function() {
    document.title = this.viewTitle
    this.$emit("on-page-title-change", this.viewTitle)
    this.$emit("on-page-icon-change", 'universe')
  },
  computed: {
    getBreadcrumbs(): string {
      return this.$data.universe.getBreadcrumbs()
    },
    viewTitle(): string { return `${this.universe.name} Universe` },
  },
  methods: {
    significantUnits(number: number) {
      return significantUnits(number)
    },
    superclusterTableRowClick(value: Supercluster){
      this.$router.push(value.locationIdentifier.generateTo('supercluster'))
    },
    customSort(items: Supercluster[], sortBy: string[], sortDesc: boolean[]) {
      return items.sort((a: Supercluster, b: Supercluster) => a.compare(b, sortBy, sortDesc))
    }
  }
})
