import Universe from "../models/Universe";
import SceneGenerator from "./SceneGenerator";

import PolarCartesian from "../models/PolarCartesian";
import Supercluster from "../models/Supercluster";
import { Vector3, StandardMaterial, Color3, SphereBuilder, Matrix, Quaternion, IcoSphereBuilder } from "@babylonjs/core";


function getTranslation(supercluster: Supercluster, universeRadius: number) {
    const cart = PolarCartesian.polarToCartesian(
        supercluster.Logitude().number,
        supercluster.Latitude().number,
        supercluster.DistanceFromCenter().number / universeRadius * 25)
    return new Vector3(cart.x, cart.y, cart.z)
}

  function getScale(supercluster: Supercluster, universeRadius: number) {
    const scale = supercluster.Radius().number * 2 / universeRadius * 500
    return new Vector3(scale, scale, scale)
}
const superclustersToRender = 1000

export default class UniverseSceneGenerator extends SceneGenerator {

    generateScene(canvas: HTMLCanvasElement, universe: Universe){
        return new Promise<void>(resolve => {
            const startTime = Date.now()
            this.initialise(canvas)

            const material = new StandardMaterial("supercluster-material", this.scene)
            material.alpha = 0.5
            material.diffuseColor = new Color3(1, 1, 1)
            material.freeze()
            
            const superclusterTemplate = IcoSphereBuilder.CreateIcoSphere('supercluster-template', {}, this.scene)
            superclusterTemplate.material = material
            
            const bufferMatrices = new Float32Array(16 * universe.SuperclusterCount())

            const universeRadius = universe.Radius().number
            universe.Superclusters()
                .sort((firstSC, secondSC) => secondSC.Radius().number - firstSC.Radius().number ) // Largest Radiuses first
                .slice(0, superclustersToRender)
                .forEach((supercluster, index) => {
                Matrix.Compose(
                    getScale(supercluster, universeRadius),
                    Quaternion.Zero(), 
                    getTranslation(supercluster, universeRadius)
                ).copyToArray(bufferMatrices, index * 16)
            })

            superclusterTemplate.thinInstanceSetBuffer("matrix", bufferMatrices, 16, false)
            superclusterTemplate.freezeWorldMatrix()
            superclusterTemplate.doNotSyncBoundingInfo = true

            // universe.Superclusters().forEach(supercluster => {
            //     const sphere = SphereBuilder.CreateSphere(supercluster.name, 
            //         { diameter: supercluster.Radius().number * 2 / universeRadius * 500}, this.scene)
            //     const scMat = material.clone(supercluster.name + ':material')
            //     scMat.diffuseColor = Color3.FromHexString(supercluster.Colour())
            //     sphere.material = scMat
            //     sphere.position = getPosition(supercluster, universeRadius)
            //     // const scale = supercluster.Radius().number / universeRadius * 100
            //     // sphere.scaling = new Vector3(scale, scale, scale)
            //     sphere.freezeWorldMatrix()

            // })

            this.scene.blockMaterialDirtyMechanism = true
            const milliseconds = Date.now() - startTime
            console.log(`Universe rendered in ${milliseconds} milliseconds. ${milliseconds / universe.SuperclusterCount()} ms per supercluster.`)
            // this.engine.runRenderLoop(() => {
                this.scene.render()
            // })
            resolve()
        })
    }
    
}

