import GeneratedObject from "../models/GeneratedObject";
import { ArcRotateCamera, Color4, Engine, HemisphericLight, Scene, Vector3 } from "@babylonjs/core";

export default abstract class SceneGenerator{
  
  startTime?: number;
  engine!: Engine;
  scene!: Scene;
  camera?: ArcRotateCamera;

  abstract generateScene(canvas: HTMLCanvasElement, object: GeneratedObject): Promise<void>

  initialise(canvas: HTMLCanvasElement){
    
    this.startTime = Date.now()
    this.engine = new Engine(canvas)
    this.scene = new Scene(this.engine)
    this.scene.clearColor = new Color4(0, 0, 0)

    this.camera = new ArcRotateCamera("cameraRotateArc", 1.5, 1.5, 50, new Vector3(0, 0, 0), this.scene)
    this.camera.setTarget(Vector3.Zero());
    this.camera.attachControl(canvas, true);

    new HemisphericLight("light", Vector3.Up(), this.scene)
    new HemisphericLight("light", Vector3.Down(), this.scene)
    
  }
}