
export default class PolarCartesian {
  static RAD2DEG = 180 / Math.PI
  static DEG2RAD = Math.PI / 180

  /**
   * Convert [lat,lon] polar coordinates to [x,y,z] cartesian coordinates
   * @param {Number} lon
   * @param {Number} lat
   * @param {Number} radius
   * @return {Vector3}
   */
  static polarToCartesian( lon: number, lat: number, radius: number ) {

    const phi = ( 90 - lat ) * this.DEG2RAD
    const theta = ( lon + 180 ) * this.DEG2RAD

    return {
      x: -(radius * Math.sin(phi) * Math.sin(theta)),
      y: radius * Math.cos(phi),
      z: radius * Math.sin(phi) * Math.cos(theta),
    }
  }

  static polarToCartesianToArray( lon: number, lat: number, radius: number ) {
    const cart = this.polarToCartesian(lon, lat, radius)
    return [cart.x, cart.y, cart.z]
  }

  /**
   * Convert [x,y,z] cartesian coordinates to polar [lat,lon]
   * @param {Vector3} coord
   * @return {Array<Number>}
   */
   static cartesianToPolar( x: number, y: number, z: number ) {

    const lon = Math.atan2( x, -z ) * this.RAD2DEG
    const length = Math.sqrt( x * x + z * z )
    const lat = Math.atan2( y, length ) * this.RAD2DEG

    return [ lon, lat ]

  }
}