













import Vue from 'vue'
import SceneGenerator from "../scenes/SceneGenerator"
import GeneratedObject from '../models/GeneratedObject';

export default Vue.extend({
  props: {
    generatredObject: {
      type: Object as () => GeneratedObject
    },
    sceneGenerator: {
      type: Object as () => SceneGenerator
    }
  },
  data: function (){
    return {
      isLoaded: false,
      isLoading: false
    }
  },
  computed: {
    canvasStyle(): string {
      return this.isLoaded || this.isLoading ? 'width: 100%; height: 100%; display:block;' : 'width: 100%; height: 100%; display:none;'
    }
  },
  methods: {
    async initialiseVisualisation() {
      console.log('start visualistation')
      const canvas = this.$refs.canvas as HTMLCanvasElement;
      if (canvas) {
        canvas.addEventListener('wheel', evt => evt.preventDefault())
        this.sceneGenerator.generateScene(canvas, this.generatredObject)
          .then(() => {
            console.log('finished visualistation')
            this.isLoaded = true
            this.isLoading = false
          })
      }
      console.log('end visualistation')
    },
    async load() {
      new Promise<void>(resolve=>{
        this.isLoading = true
        resolve()
      }).then(this.initialiseVisualisation)
    }
  },
  mounted() {
    window.onbeforeunload = () => (this.sceneGenerator.engine.dispose);
  },
})
